body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal-wrapper {
  position: fixed !important;
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
}

.modal-backdrop {
  position: fixed !important;
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
  z-index: 100 !important;
  background-color: rgba(0, 0, 0, 0.3) !important;
}

.modal-box {
  position: relative !important;
  float: left !important;
  height: 100% !important;
  width: 20% !important;
  overflow-y: auto !important;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25) !important;
  z-index: 101 !important;
  text-align: center !important;
  background-color: #AB83D3 !important;
}

.div-nav-bar {
  height: 100%;
  width: 100%;
  display: table;
}

.div-nav-elements {
  display: table-cell;
  vertical-align: middle;
}

ul {
  list-style: none;
  margin-bottom: 20px;
}

ul li {
  padding: 20px;
}

ul li a {
  color: black;
  text-decoration: none;
  font-size: xx-large;
  font-family: 'Slabo 27px', serif;
  font-weight: 300;
}

ul li a:hover {
  color: grey;
}

.icon {
  float: left;
  margin: 20px;
  color: black;
}

.icon:hover {
  color: black;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .modal-box {
    width: 40% !important;
  }
}

.title {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: default;
}

.lists {
  color: black;
  font-size: 200px;
  font-weight: 400;
  font-family: 'Slabo 27px', serif;
}

@media screen and (max-width: 500px) {
  .recipes {
    font-size: 900%;
  }
}


.container{
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: default;
  font-size: 25px;
  font-weight: 400;
  font-family: 'Slabo 27px', serif;
}

@media screen and (max-width: 500px) {
  .recipes {
    font-size: 900%;
  }
}
