.deck {
  width: 100%;
  margin: 0 !important;
  margin-bottom: 5% !important;
}

.item_card {
  margin-left: 6% !important;
  margin-right: 6% !important;
}

.item_card_hidden {
  margin-left: 6% !important;
  margin-right: 6% !important;
  visibility: hidden;
}

.item_card:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 1);
}

.wrap {
  text-align: center;
  width: 100vw !important;
}

.item-deck {
